.maxiscope {
  position: relative;
}
.maxiscope::before {
  content: "";
  position: absolute;
  border-radius: 50%;
  width: 400px;
  height: 400px;
  background-color: rgba(139, 103, 103, 0.1);
  filter: blur(90px);
  left: -81px;
  top: 91px;
  z-index: -1;
}
.maxiscope__title {
  margin: 0 auto 22px;
  max-width: 710px;
}
.maxiscope__subtitle {
  margin: 0 auto 50px;
  max-width: 360px;
}
.maxiscope__content {
  display: grid;
  grid-template-columns: 235px auto 235px;
  border: 1px solid #535353;
  border-radius: 10px;
  background-color: rgba(12, 12, 12, 0.5);
}
.maxiscope__col {
  display: grid;
  grid-template-rows: 1fr 1fr;
}
.maxiscope__item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.maxiscope__item p {
  color: #e0e4eb;
}
.maxiscope__item:first-child {
  border-bottom: 1px solid #535353;
}
.maxiscope__value {
  margin-bottom: 4px;
  font-family: "AlumniSans", sans-serif;
  font-weight: 700;
  font-size: 32px;
  color: #d2232a;
}
.maxiscope__img {
  margin-top: -1px;
  margin-bottom: -1px;
}
.maxiscope__img img {
  height: calc(100% + 2px);
}
.maxiscope__img picture {
  display: flex;
}

.comfort {
