}
.tests__preview::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  border-radius: 13px;
  width: 100%;
  height: 100%;
  background-color: rgba(12, 12, 12, 0.55);
