.characteristics {
  position: relative;
  overflow: hidden;
  background-color: #141111;
}
.characteristics::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  width: 581px;
  height: 581px;
  background-color: rgba(139, 103, 103, 0.25);
  filter: blur(250px);
  right: -157px;
  top: -291px;
}
.characteristics__title {
  margin: 0 auto 22px;
  max-width: 820px;
}
.characteristics__subtitle {
  margin-bottom: 50px;
}
.characteristics__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0 28px;
}
.characteristics__item {
  border: 1px solid #535353;
  border-radius: 10px;
  padding: 28px;
}
.characteristics__img {
  margin-bottom: 16px;
}
.characteristics__name {
  margin-bottom: 16px;
  font-family: "AlumniSans", sans-serif;
  font-weight: 500;
  font-size: 36px;
  color: #d2232a;
